
import Vue from 'vue'

export default Vue.extend({
  name: 'ActionsBar',
  data: () => ({
    items: [
      { title: 'Секция №1', value: 'SectionOne' },
      { title: 'Секция №2', value: 'SectionTwo' },
      { title: 'Секция №3', value: 'SectionThree' }
    ],
    closeOnClick: true
  }),
  methods: {
    createSection (value: string) {
      if (value === 'SectionOne') this.$emit('createOne')
      else if (value === 'SectionTwo') this.$emit('createTwo')
      else if (value === 'SectionThree') this.$emit('createThree')
    }
  }
})
