
import Vue from 'vue'
import HelloWorld from './components/HelloWorld.vue'
/*
* <v-col class="d-flex" cols="auto">
          <v-btn class="align-self-center" @click="saveInformation">Сохранить</v-btn>
          <v-btn class="align-self-center" @click="loadInformation">Загрузить</v-btn>
        </v-col>
        <v-col cols="auto">
          <div class="align-self-center">
            <label>Редактирование</label>
            <v-switch @click="changeEdit"></v-switch>
          </div>

        </v-col> */
export default Vue.extend({
  name: 'App',

  components: {
    HelloWorld
  },

  data: () => ({
    edit: true,
    items: [
      { title: 'Загрузить', value: 'loadInformation' },
      { title: 'Сохранить', value: 'saveInformation' },
      { title: 'Редактирование', value: 'editPage', atr: 0, page: ['Редактирование разрешено', 'Редактирование запрещено'] }
    ],
    closeOnClick: true
    //
  }),
  methods: {
    action (value : any) {
      if (value.title === 'Загрузить') {
        this.loadInformation()
      } else if (value.title === 'Сохранить') {
        this.saveInformation()
      } else {
        if (value.atr === 0) value.atr = 1
        else value.atr = 0
        this.changeEdit()
      }
    },
    changeEdit () {
      this.edit = !this.edit
    },
    saveInformation () {
      const site = this.$store.state.site.array
      console.log(site)
      localStorage.setItem('site', JSON.stringify(site))
    },
    loadInformation () {
      const site = localStorage.getItem('site')
      console.log(site)
      if (site) { console.log('retrievedObject: ', JSON.parse(site)) }
      location.reload()
    }
  },
  mounted () {
    const site = localStorage.getItem('site')
    if (site) { this.$store.state.site.array.push(...JSON.parse(site)) }
  }
})
