

import Vue from 'vue'
import { mapActions } from 'vuex'
import { MutationType } from '@/store'

export default Vue.extend({
  name: 'SectionOne',
  props: ['main', 'id', 'editable'],
  data () {
    return {
      mainValue: {
        header: 'fsd',
        description: 'fds'
      }
    }
  },
  methods: {
    ...mapActions([
      MutationType.CHANGE_SECTION_HEADER,
      MutationType.CHANGE_SECTION_DESCRIPTION
    ]),
    delSection () {
      this.$emit('deletesection')
    },
    moveDown () {
      this.$emit('move-down')
    },
    moveUp () {
      this.$emit('move-up')
    },
    changeHeader () {
      const payload = { idSec: this.idEl, valueString: this.mainValue.header }
      this.ChangeSectionHeader(payload)
    },
    changeDescription (idSec: number) {
      const payload = { idSec: this.idEl, valueString: this.mainValue.description }
      this.ChangeSectionDescription(payload)
    }
  },
  computed: {
    doneTodos () {
      return this.$store.getters.getSiteMainValueById(this.id)
    },
    idEl () {
      return this.id
    }
  },
  mounted: function () {
    this.mainValue = this.doneTodos
  }
})
