import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)
export enum MutationType{
  ADD_SECTION_ONE='AddSectionOne',
  ADD_SECTION_TWO='AddSectionTwo',
  ADD_SECTION_THREE='AddSectionThree',
  DELETE_SECTION='DeleteSection',
  MOVE_UP='MoveUp',
  MOVE_BOTTOM='MoveBottom',
  ADD_CARD='AddCard',
  DELETE_CARD='DeleteCard',
  CHANGE_CARD_HEADER='ChangeCardHeader',
  CHANGE_CARD_DESCRIPTION='ChangeCardDescription',
  CHANGE_CARD_IMAGE='ChangeCardImage',
  CHANGE_SECTION_HEADER='ChangeSectionHeader',
  CHANGE_SECTION_DESCRIPTION='ChangeSectionDescription',
  GET_FILMS='GetFilms',
  GET_RANDOM_FILMS='GetRandomFilm'

}
interface Card {
  id: number,
  header?: string,
  description?: string,
  img?: string
  imgWidth?:number
}
interface Section {
  id: number
  name: string,
  main: {
    header?: string,
    description?: string
  }
  cards: Card[]
  films: object[]
}

interface site{
  array:Section[],
}
export default new Vuex.Store({
  state: {
    site: <site>{
      array: []
    }

  },
  getters: {
    getSiteArray (state) {
      return state.site.array
    },
    getSiteCardsById: (state) => (id:number) => {
      return state.site.array[id].cards
    },
    getSiteMainValueById: (state) => (id:number) => {
      return state.site.array[id].main
    },
    getFilmsArray: (state) => (id:number) => {
      return state.site.array[id].films
    }
  },
  mutations: {
    [MutationType.ADD_SECTION_ONE]: (state) => {
      const length = state.site.array.length === 0 ? 0 : state.site.array.length - 1
      if (state.site.array.length !== 0) {
        const payload :Section = {
          id: state.site.array[length].id + 1,
          name: 'SectionOne',
          main: {
            header: 'Пример Заголовка Секции 1',
            description: 'Пример Описания Секции 1'
          },
          cards: [],
          films: []
        }
        state.site.array.push(payload)
      } else {
        const payload = {
          id: 0,
          name: 'SectionOne',
          main: {
            header: 'Пример Заголовка Секции 1',
            description: 'Пример Описания Секции 1'
          },
          cards: [],
          films: []
        }
        state.site.array.push(payload)
      }
    },
    [MutationType.ADD_SECTION_TWO]: (state) => {
      const length = state.site.array.length === 0 ? 0 : state.site.array.length - 1
      if (state.site.array.length !== 0) {
        const payload = {
          id: state.site.array[length].id + 1,
          name: 'SectionTwo',
          cards: [],
          main: {},
          films: []
        }
        state.site.array.push(payload)
      } else {
        const payload = {
          id: 0,
          name: 'SectionTwo',
          cards: [],
          main: {},
          films: []
        }
        state.site.array.push(payload)
      }
    },
    [MutationType.ADD_SECTION_THREE]: (state) => {
      const lengthSite = state.site.array.length === 0 ? 0 : state.site.array.length - 1
      if (state.site.array.length !== 0) {
        const payload = {
          id: state.site.array[lengthSite].id + 1,
          name: 'SectionThree',
          cards: [],
          main: {},
          films: []
        }
        state.site.array.push(payload)
      } else {
        const payload = {
          id: 0,
          name: 'SectionThree',
          cards: [],
          main: {},
          films: []
        }
        state.site.array.push(payload)
      }
    },
    [MutationType.MOVE_UP]: (state, idSec:number) => {
      if (idSec > 0 && idSec !== state.site.array.length) {
        const oldValue = state.site.array[idSec]
        const newValue = state.site.array[idSec - 1]
        const oldId = oldValue.id
        const newId = newValue.id
        state.site.array[idSec] = newValue
        state.site.array[idSec - 1] = oldValue
        state.site.array[idSec].id = oldId
        state.site.array[idSec - 1].id = newId
      }
    },
    [MutationType.MOVE_BOTTOM]: (state, idSec:number) => {
      if (idSec >= 0) {
        const oldValue = state.site.array[idSec]
        const newValue = state.site.array[idSec + 1]
        const oldId = oldValue.id
        const newId = newValue.id
        state.site.array[idSec] = newValue
        state.site.array[idSec + 1] = oldValue
        state.site.array[idSec].id = oldId
        state.site.array[idSec + 1].id = newId
      }
    },
    [MutationType.DELETE_SECTION]: (state, idSec:number) => {
      state.site.array.splice(idSec, 1)
    },
    [MutationType.DELETE_CARD]: (state, payload) => {
      state.site.array[payload.id].cards.splice(payload.ids, 1)
    },
    [MutationType.CHANGE_SECTION_HEADER]: (state, payload) => {
      state.site.array[payload.idSec].main.header = payload.valueString
    },
    [MutationType.CHANGE_SECTION_DESCRIPTION]: (state, payload) => {
      state.site.array[payload.idSec].main.description = payload.valueString
    },
    [MutationType.CHANGE_CARD_HEADER]: (state, payload) => {
      state.site.array[payload.idSec].cards[payload.idEl].header = payload.valueString
    },
    [MutationType.CHANGE_CARD_DESCRIPTION]: (state, payload) => {
      state.site.array[payload.idSec].cards[payload.idEl].description = payload.valueString
    },
    [MutationType.CHANGE_CARD_IMAGE]: (state, payload) => {
      state.site.array[payload.idSec].cards[payload.idEl].img = payload.valueString
    },
    [MutationType.ADD_CARD]: (state, payload) => {
      const val = state.site.array[payload.idSec].cards
      let ind
      if (val.length > 0) { ind = val.length - 1 } else { ind = 0 }
      if (val.length > 0) {
        const payloadCard = {
          id: val[ind].id + 1,
          header: 'Заголовок пример',
          description: 'Описание пример',
          img: 'https://cdn.vuetifyjs.com/images/cards/cooking.png'
        }
        state.site.array[payload.idSec].cards.push(payloadCard)
      } else {
        const payloadCard = {
          id: 0,
          header: 'Заголовок пример',
          description: 'Описание пример',
          img: 'https://cdn.vuetifyjs.com/images/cards/cooking.png'
        }
        state.site.array[payload.idSec].cards.push(payloadCard)
      }
    },
    [MutationType.GET_RANDOM_FILMS]: (state, payload) => {
      state.site.array[payload.idSec].films.push(payload.resolve)
    }
  },
  actions: {
    [MutationType.CHANGE_SECTION_HEADER]: async ({ commit }, payload: { idSec:number, valueString: string }) => {
      commit(MutationType.CHANGE_SECTION_HEADER, payload)
    },
    [MutationType.CHANGE_SECTION_DESCRIPTION]: async ({ commit }, payload: { idSec:number, valueString: string }) => {
      commit(MutationType.CHANGE_SECTION_DESCRIPTION, payload)
    },
    [MutationType.CHANGE_CARD_HEADER]: async ({ commit }, payload: { idSec:number, idEl:number, valueString: string }) => {
      commit(MutationType.CHANGE_CARD_HEADER, payload)
    },
    [MutationType.CHANGE_CARD_DESCRIPTION]: async ({ commit }, payload: { idSec:number, idEl:number, valueString: string }) => {
      commit(MutationType.CHANGE_CARD_DESCRIPTION, payload)
    },
    [MutationType.CHANGE_CARD_IMAGE]: async ({ commit }, payload: { idSec:number, idEl:number, valueString: string }) => {
      commit(MutationType.CHANGE_CARD_IMAGE, payload)
    },
    [MutationType.ADD_CARD]: async ({ commit }, payload:{ idSec:number}) => {
      commit(MutationType.ADD_CARD, payload)
    },
    [MutationType.DELETE_CARD]: async ({ commit }, payload:{ id:number, ids: number}) => {
      commit(MutationType.ADD_CARD, payload)
    },
    [MutationType.GET_RANDOM_FILMS]: async ({ commit }, id) => {
      const response = await fetch('https://api.kinopoisk.dev/v1/movie/random', {
        headers: {
          Accept: 'application/json',
          'X-Api-Key': '5EAYHA1-9V94878-MDFY1FV-4FKPX7R'
        }
      }).then((resolve) => { return resolve.json() }).then((data) => {
        return data
      })
      commit(MutationType.GET_RANDOM_FILMS, { idSec: id, resolve: response })
    }
  },
  modules: {
  }
})
