
import Vue from 'vue'
import { mapActions } from 'vuex'
import { MutationType } from '@/store'

export default Vue.extend({
  name: 'SectionThree',
  props: ['cards', 'editable', 'id'],
  data () {
    return {
      filmsValue: []
    }
  },
  methods: {
    ...mapActions([
      MutationType.GET_RANDOM_FILMS
    ]),
    delSection () {
      this.$emit('deletesection', this.id)
    },
    moveDown () {
      this.$emit('move-down')
    },
    moveUp () {
      this.$emit('move-up')
    },
    loadRandom () {
      this.$store.dispatch(MutationType.GET_RANDOM_FILMS, this.id)
    }
  },
  beforeCreate () {
    // this.$store.dispatch(MutationType.GET_RANDOM_FILMS)
  },
  mounted: function () {
    this.filmsValue = this.$store.getters.getFilmsArray(this.id)
  }
})
