
import Vue from 'vue'
import SectionOne from '@/components/SectionOne.vue'
import SectionTwo from '@/components/SectionTwo.vue'
import SectionThree from '@/components/SectionThree.vue'
import ActionsBar from '@/components/ActionsBar.vue'
import { mapMutations } from 'vuex'
import { MutationType } from '@/store'

export default Vue.extend({
  name: 'HelloWorld',
  props: ['editable'],
  components: { SectionThree, ActionsBar, SectionOne, SectionTwo },
  data: () => ({
    array: []
  }),
  mounted () {
    this.array = this.$store.getters.getSiteArray
  },
  methods: {
    ...mapMutations([
      MutationType.ADD_SECTION_ONE,
      MutationType.ADD_SECTION_TWO,
      MutationType.ADD_SECTION_THREE,
      MutationType.MOVE_UP,
      MutationType.MOVE_BOTTOM,
      MutationType.DELETE_SECTION
    ]),
    createSectionOne () {
      this.AddSectionOne()
    },
    createSectionTwo () {
      this.AddSectionTwo()
    },
    createSectionThree () {
      this.AddSectionThree()
    },
    moveUp (id:number) {
      this.MoveUp(id)
    },
    moveDown (id:number) {
      this.MoveBottom(id)
    },
    deleteSection (id:number) {
      this.DeleteSection(id)
    }
  }
})
