
import Vue from 'vue'
import { mapActions } from 'vuex'
import { MutationType } from '@/store'

interface data {
  cardsValue:{
    header: string,
    description: string,
    img: string
    imgWidth: number
  }[]
}

export default Vue.extend({
  name: 'SectionTwo',
  props: ['cards', 'editable', 'id'],
  data ():data {
    return {
      cardsValue: []
    }
  },
  methods: {
    ...mapActions([
      MutationType.CHANGE_CARD_HEADER,
      MutationType.CHANGE_CARD_DESCRIPTION,
      MutationType.CHANGE_CARD_IMAGE,
      MutationType.ADD_CARD
    ]),
    changeHeader (ids: number) {
      const payload = { idSec: this.id, idEl: ids, valueString: this.cardsValue[ids].header }
      this.ChangeCardHeader(payload)
    },
    changeDescription (ids: number) {
      const payload = { idSec: this.id, idEl: ids, valueString: this.cardsValue[ids].description }
      this.ChangeCardDescription(payload)
    },
    changeImage (ids: number) {
      const payload = { idSec: this.id, idEl: ids, valueString: this.cardsValue[ids].img }
      this.ChangeCardImage(payload)
    },
    deleteCard (ids:number) {
      this.$store.state.site.array[this.id].cards.splice(ids, 1)
    },
    createCard () {
      this.AddCard({ idSec: this.id })
    },
    delSection () {
      this.$emit('deletesection', this.id)
    },
    moveDown () {
      this.$emit('move-down')
    },
    moveUp () {
      this.$emit('move-up')
    }
  },
  mounted: function () {
    this.cardsValue = this.$store.getters.getSiteCardsById(this.id)
  }
})
